:root {
  --primary-color: #FFDDC9;
  --detail-color: #00E6E6;
  --text-color: #6B6B6B;
  --secondary-color: #E5F3F5;
  --accent-color: #1A1B1D;
  --detail-color-opaque: #E5F3F5;
}

body, html {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  width: 100%;
  height: 100%;
}

button {
  font-family: 'Roboto', sans-serif;
}
